import popup from '~/components/Popup/popup.vue'
import axios from 'axios'

export default {
  middleware: 'authenticated',

  name: 'Favorite',

  components: { popup },

  data: () => ({
    absolute: true,
    overlay: false,
    dialog: false,
    dialogSuc: true,
    favorite: [],
    tags: [
      { tagName: 'Drink' },
      { tagName: 'Main Disc' },
      { tagName: 'Burger' },
      { tagName: 'Noodle' },
      { tagName: 'Fruit' },
      { tagName: 'Cheese' },
      { tagName: 'Cream' },
      { tagName: 'Soft Drink' },
      { tagName: 'Steak' },
      { tagName: 'Milk' },
      { tagName: 'Ice cream' },
    ],
    breadcrumbs: [
      {
        text: 'หน้าแรก',
        disabled: false,
        href: '/',
      },
      {
        text: 'เมนูโปรด',
        disabled: true,
        href: 'favorite',
      },
    ],
  }),
  methods: {
    async getFavoriteRecipe() {
      console.log('geting recipe ...')
      this.$store.commit('SET_DIALOG_LOADING', true)
      await axios
        .get(
          'http://127.0.0.1:5000/api/favorite/' + this.$store.getters.getUserID
        )
        .then((res) => {
          this.favorite = res.data
          console.log('data here', this.favorite)
        })
      console.log('finished ...')
      this.$store.commit('SET_DIALOG_LOADING', false)
    },

    ClickRecipe(id) {
      console.log(id)
      this.$router.push({ path: 'recipe', query: { rid: id } })
    },

    async delFavoriteRecipe(fav_id, recipe_id) {
      this.dialog = false
      console.log('deleteing ...')
      this.$store.commit('SET_DIALOG_LOADING', true)
      await axios
        .delete(
          'http://127.0.0.1:5000/api/favorite/' + fav_id + '/' + recipe_id
        )
        .then((res) => {
          console.log(res)
        })
      console.log('finished ...')
      this.$store.commit('SET_DIALOG_LOADING', false)
      // this.$forceUpdate()
      // setInterval(() => {
      //       this.dialogSuc = true
      //     }, 3000);
    },

    AddDialog() {
      this.dialog = true
    },
  },
  async mounted() {
    await this.getFavoriteRecipe()
  },
}
